.footer__wrap {
    width: 100%;

    background: white;
    backdrop-filter: blur(20px);

    position: absolute;
    bottom: 0;
}

.footer {
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    display: flex;

    align-items: center;
    justify-content: space-around;
    /* padding-left: 10px; */
